    font-family: "Josefin Sans";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    background-color: #901E3E;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 0 0 0;
    min-height: 100vh;
    font-family: "Josefin Sans", sans-serif;
}

header {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    letter-spacing: 1rem;
    color: #FFF287;
}

nav{
    display: flex;
    gap: 4rem;
}

nav button{
    font-size: 1.5rem;
    border: none;
    background: none;
    padding: 1rem;
    cursor: pointer;
    border-radius: 8px;
    font-family: inherit;
    color: inherit;
}

nav button.active,
nav button:hover{
    text-decoration: underline;
    line-height: 1rem;
    text-underline-offset: 10px;
}

#content{
    width: 100%;
    flex: 1;
}

/* Home Page */
.home-page{
    display: grid;
    grid-template-columns: 2fr 400px;
    grid-template-rows: 1fr 1fr;
    padding: 20px 20px 20px 60px;
    row-gap: 10px;
    background-color: #511D43;
    color: white;
}

.headline{
    display: flex;
    gap: 2rem;
    align-items: center;
}
.headline img{
    border-radius: 8px;
}
.headline .text {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.headline .text p{
    max-width: 80%;
}

.sub-headline{
    display: flex;
    gap: 2rem;
    align-items: center;
    justify-content: end;
}
.sub-headline img{
    border-radius: 8px;
}
.sub-headline .text {
    display: flex;
    flex-direction: column;
    align-items: end;
    gap: 1rem;
    text-align: end;
}
.sub-headline .text p{
    max-width: 80%;
}

.banner{
    grid-area: 1 / 2 / 3 / 3;
    display: flex;
    justify-content: center;
}
.banner img{
    border-radius: 8px;
}

.text h2{
    font-size: 2rem;
}

/* Menu Page */
.menu-page{
    width: 100%;
    padding: 1rem;
    background-color: #511D43;
    color: white;
}

.menu-page img{
    border-radius: 8px;
}

.menu{
    display: flex;
    align-items: center;
    gap: 3rem;
    height: 330px;
    justify-content: center;
}
.menu h2{
    color: #FFF287;
}

.description{
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.description p{
    height: 100%;
    line-height: 1.3rem;
}

/* About Page */
.about-page{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    height: 80vh;
}
.about-page .container{
    width: 100%;
    padding: 1rem;
    background-color: #511D43;
    color: white;
}
.about-page .container:nth-child(2){
    display: flex;
    flex-direction: column;
    gap: 2rem;
    width: 100%;
    height: 80%;
    padding: 1rem;
    background-color: #511D43;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}
.about-page .container:nth-child(2) h2{
    width: 75%;
    line-height: 3rem;
    letter-spacing: 2px;
    text-align: center;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,2BAA2B;IAC3B,4CAAqD;AACzD;;AAEA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,kIAAkI;IAClI,yBAAyB;IACzB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,mBAAmB;IACnB,iBAAiB;IACjB,uCAAuC;AAC3C;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,WAAW;IACX,oBAAoB;IACpB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,gBAAgB;IAChB,aAAa;IACb,eAAe;IACf,kBAAkB;IAClB,oBAAoB;IACpB,cAAc;AAClB;;AAEA;;IAEI,0BAA0B;IAC1B,iBAAiB;IACjB,2BAA2B;AAC/B;;AAEA;IACI,WAAW;IACX,OAAO;AACX;;AAEA,cAAc;AACd;IACI,aAAa;IACb,gCAAgC;IAChC,2BAA2B;IAC3B,4BAA4B;IAC5B,aAAa;IACb,yBAAyB;IACzB,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,mBAAmB;AACvB;AACA;IACI,kBAAkB;AACtB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;AACA;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,mBAAmB;IACnB,oBAAoB;AACxB;AACA;IACI,kBAAkB;AACtB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,gBAAgB;IAChB,SAAS;IACT,eAAe;AACnB;AACA;IACI,cAAc;AAClB;;AAEA;IACI,wBAAwB;IACxB,aAAa;IACb,uBAAuB;AAC3B;AACA;IACI,kBAAkB;AACtB;;AAEA;IACI,eAAe;AACnB;;AAEA,cAAc;AACd;IACI,WAAW;IACX,aAAa;IACb,yBAAyB;IACzB,YAAY;AAChB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,aAAa;IACb,uBAAuB;AAC3B;AACA;IACI,cAAc;AAClB;;AAEA;IACI,UAAU;IACV,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;AACA;IACI,YAAY;IACZ,mBAAmB;AACvB;;AAEA,eAAe;AACf;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,YAAY;AAChB;AACA;IACI,WAAW;IACX,aAAa;IACb,yBAAyB;IACzB,YAAY;AAChB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,WAAW;IACX,WAAW;IACX,aAAa;IACb,yBAAyB;IACzB,YAAY;IACZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;AACA;IACI,UAAU;IACV,iBAAiB;IACjB,mBAAmB;IACnB,kBAAkB;AACtB","sourcesContent":["@font-face {\n    font-family: \"Josefin Sans\";\n    src: url('./fonts/JosefinSans-VariableFont_wght.ttf');\n}\n\n*{\n    margin: 0;\n    padding: 0;\n    box-sizing: border-box;\n}\n\nbody{\n    font-family: system-ui, \"Segoe UI\", Roboto, Helvetica, Arial, sans-serif, \"Apple Color Emoji\", \"Segoe UI Emoji\", \"Segoe UI Symbol\";\